import React, { Component, lazy, Suspense } from "react";

import { connect } from "react-redux";
import Swal from "sweetalert2";
import ReactTooltip from "react-tooltip";
import { reset } from "redux-form";
import {
  EditPindahBarang,
  getListPindahBarang,
} from "../actions/pindahBarangAction";
import { getUUID } from "../actions/fakturAction";
import {
  finishSend,
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import PrintNota from "./Component/PrintNota";
import { gagal, warning } from "../Alert";
import { deleteIndex } from "../deleteLocalStorage";
import HeadPindahBarang from "./Component/HeadPindahBarang";
import ModalMasterData from "../DataMaster/Component/ModalMasterData";
import { AxiosMasterPost } from "../Axios";
import Skeleton from "react-loading-skeleton";
import HeaderCard from "../HeaderCard";
import { formatGram, formatRp, getUserID } from "../helper";

const FormTambahBarang = lazy(() => import("./Component/FormTambahBarang"));
const maptoState = (state) => {
  return {
    listBarang: state.provinsi.pindahBarang,
    listEditPindahBarang: state.provinsi.listEditPindahBarang,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumRp: state.perhitungan.sumRp,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    onSend: state.provinsi.onSend,
  };
};

class PindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dari: [],
      kepada: [],
      tanggal: "",
      dariIsi: "",
      ke: "",
      noBon: "",
      kategori: "",
      kodeJenis: "",
      keterangan: "",
      edited: false,
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kadar_modal",
          text: "Kode Awal",
          formatter: (data) => formatGram(data),
        },

        {
          dataField: "kadar_cetak",
          text: "Cap Kode",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data),
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data),
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
          align: "right",
          formatter: (data) => formatGram(data),
          headerAlign: "right",
        },
        {
          dataField: "kadar_jual",
          text: "Kode Tukar",
          align: "right",
          formatter: (data) => formatGram(data, 2),
          headerAlign: "right",
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data, 3),
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos (Rp)",
          align: "right",
          headerAlign: "right",
          formatter: (cell) => {
            return <span>Rp. {formatRp(cell)}</span>;
          },
        },
        {
          dataField: "qty",
          text: "Qty",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  type="button"
                  className="btn btn-danger"
                  onClick={() =>
                    deleteIndex(
                      "pindahBarang",
                      rowIndex,
                      this.props.dispatch,
                      getListPindahBarang()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
    };
  }
  getSumData() {
    this.props.dispatch(getNetto("pindahBarang"));
    this.props.dispatch(getBruto("pindahBarang"));
    this.props.dispatch(getRp("pindahBarang"));
    this.props.dispatch(getPkg("pindahBarang"));
    this.props.dispatch(getQty("pindahBarang"));
  }
  setEdit(row) {
    this.props.dispatch(EditPindahBarang(row));
    this.setState({
      edited: true,
    });
  }
  getTabelData() {
    this.props.dispatch(getListPindahBarang());
  }
  componentDidMount() {
    this.props.dispatch(getListPindahBarang());
    this.props.dispatch(getUUID());
    this.getSumData();
    this.dataEditPindahBarang();
  }
  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT |DEFAULT |DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
    });
    var barang1 = JSON.parse(localStorage.getItem("pindahBarang")) || [];
    var simpan = {
      kode_kategori: result_explode[5],
      kode_jenis: result_explode[0],
      nama_jenis: result_explode[4],
      kode_barcode: "-",
      qty: data.qty || 1,
      p: 0,
      k: 0,
      gross: parseFloat(data.bruto) + parseFloat(data.pkg) || 0,
      pkg: parseFloat(data.pkg) || 0,
      bruto: parseFloat(data.bruto) || 0,
      kadar_cetak: (parseFloat(data.kadar) || 0).toString(),
      kadar_modal: parseFloat(data.kadarTransaksi) || 0,
      kadar_jual: parseFloat(data.kadarJual) || 0,
      netto: parseFloat(data.netto) || 0,
      ongkos_rp: parseFloat(data.ongkos) || 0,
    };
    barang1.push(simpan);
    localStorage.setItem("pindahBarang", JSON.stringify(barang1));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Transaksi Berhasil",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("FormTambahBarang")))
      .then(() => this.props.dispatch(getListPindahBarang()))
      .then(() => this.getSumData())
      .catch((err) =>
        gagal(`Gagal Simpan Data, Detail : ${err.response.data}`)
      );
  }

  SubmitPindah(data) {
    if (localStorage.getItem("pindahBarang") === null) {
      warning("Mohon Tambahkan Barang Yang akan dipindahkan");
      return false;
    }
    var isi = {
      no_pindah: this.props.noFaktur,
      no_bon: data.noBon,
      tanggal: data.tanggal,
      lokasi_asal: data.dari,
      lokasi_tujuan: data.ke,
      keterangan: "PINDAH BARANG",
      detail_barang: JSON.parse(localStorage.getItem("pindahBarang")),
    };
    this.props.dispatch(progressSend());
    console.log(JSON.stringify(isi));
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("pindahBarang"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        { content: ticket.qty, styles: { halign: "right" } },
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        { content: ticket.kadar_modal, styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        { content: ticket.pkg, styles: { halign: "right" } },
        { content: formatGram(ticket.gross), styles: { halign: "right" } },
        { content: formatRp(ticket.ongkos_rp), styles: { halign: "right" } },
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      { content: "" },
      { content: "Grand Total" },
      {
        content: table
          .map((list) => parseFloat(list.qty))
          .reduce((a, b) => a + b, 0),
        styles: {
          halign: "right",
        },
      },
      {
        content: formatGram(
          table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
        ),
        styles: {
          halign: "right",
        },
      },
      { content: "" },
      {
        content: formatGram(
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0),
          3
        ),
        styles: {
          halign: "right",
        },
      },
      {
        content: table
          .map((list) => parseFloat(list.pkg))
          .reduce((a, b) => a + b, 0),
        styles: {
          halign: "right",
        },
      },
      {
        content: formatGram(
          table.map((list) => parseFloat(list.gross)).reduce((a, b) => a + b, 0)
        ),
        styles: {
          halign: "right",
        },
      },
      {
        content: formatRp(
          table
            .map((list) => parseFloat(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
        ),
        styles: {
          halign: "right",
        },
      },
    ];
    // tableRows.push(footer);
    var columTable = [
      [
        { content: "No" },
        { content: "Kode Jenis" },
        {
          content: "Qty",
          styles: {
            halign: "right",
          },
        },
        {
          content: "Bruto (Gr)",
          styles: {
            halign: "right",
          },
        },
        {
          content: "Kode Awal",
          styles: {
            halign: "right",
          },
        },
        {
          content: "Netto (Gr)",
          styles: {
            halign: "right",
          },
        },
        {
          content: "Pkg (Gr)",
          styles: {
            halign: "right",
          },
        },
        {
          content: "Gross (Gr)",
          styles: {
            halign: "right",
          },
        },
        {
          content: "Ongkos (Rp)",
          styles: {
            halign: "right",
          },
        },
      ],
    ];

    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("pindah-barang/simpan", isi)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "Lokasi",
              data.dari + " - " + data.ke,
              "Nomor Bon",
              data.noBon,
              "",
              "",
              getUserID(),
              data.tanggal,
              "",
              columTable,
              "PINDAH BARANG",
              tableRows,
              footer
            )
          )
          .then(() => localStorage.removeItem("pindahBarang"))
          .then(() => this.props.dispatch(reset("headPindahBarang")))
      )
      .then(() => this.props.dispatch(finishSend()))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.props.dispatch(getListPindahBarang()))
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text : err?.response?.data || "Terjadi Kesalahan Internal, Silahkan Hubungi Admin",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => console.log(err))
          .then(() => this.props.dispatch(finishSend()))
      );
    this.props.dispatch(getListPindahBarang());
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  resetField() {
    localStorage.removeItem("editPindahBarang");
    localStorage.removeItem("pindahBarang");
    this.props.dispatch(getUUID());
    this.props.dispatch(getListPindahBarang());
    this.setState({
      tanggal: "",
      dariIsi: "",
      ke: "",
      noBon: "",
      kategori: "",
      kodeJenis: "",
      keterangan: "",
    });
  }
  dataEditPindahBarang() {
    var data = JSON.parse(localStorage.getItem("editPindahBarang")) || [];
    this.setState({
      dariIsi: data.kodeLokasiAsal,
      ke: data.kodeLokasiTujuan,
      tanggal: data.tanggal,
      keterangan: data.keterangan,
      noBon: data.noBon,
    });
    this.props.dispatch(getListPindahBarang());
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Pindah Barang" />
              <div className="card-body">
                <div className="row">
                  <ModalMasterData
                    content={
                      <Suspense
                        fallback={
                          <Skeleton width={"100%"} height={50} count={3} />
                        }
                      >
                        <FormTambahBarang
                          onSubmit={(data) => this.handleSubmit(data)}
                          listBarang={this.props.listBarang}
                          setEdit={this.state.setEdit}
                        />
                      </Suspense>
                    }
                    title="Tambah Data Pindah Barang"
                  />
                  <div className="col-lg-12">
                    <HeadPindahBarang
                      onSubmit={(data) => this.SubmitPindah(data)}
                      onSend={this.props.onSend}
                      tambahModal={this.tambahModal}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(PindahBarang);
